import * as jose from 'jose';

const CONCURRENT = 6; // Cloudflare limit per invocation

interface Env {
  JWT_SECRET: string;
}

interface JWTPayload {
  url: string;
  countPerBatch: number;
  maxBatches: number;
  exp: number;
}

async function verifyLoadTestToken(token: string, secret: string): Promise<JWTPayload> {
  const key = new TextEncoder().encode(secret);
  const { payload } = await jose.jwtVerify(token, key, { algorithms: ['HS256'] });
  if (!payload.url || typeof payload.countPerBatch !== 'number' || typeof payload.maxBatches !== 'number') {
    throw new Error('Invalid token payload');
  }
  return payload as unknown as JWTPayload;
}

async function runBatch(targetUrl: string, count: number): Promise<{
  sent: number;
  ok: number;
  errors: number;
  latencies: number[];
}> {
  const latencies: number[] = [];
  let ok = 0;
  let errors = 0;
  const queue = Array.from({ length: count }, (_, i) => i);

  while (queue.length > 0) {
    const chunk = queue.splice(0, CONCURRENT);
    const results = await Promise.all(
      chunk.map(async () => {
        const start = Date.now();
        try {
          const res = await fetch(targetUrl, {
            method: 'GET',
            headers: {
              'User-Agent': 'UptimeTR-LoadTest/1.0',
              Accept: 'text/html,application/xhtml+xml,application/xml;q=0.9,*/*;q=0.8',
            },
            redirect: 'follow',
          });
          const ms = Date.now() - start;
          try {
            if (res.body) await res.arrayBuffer();
          } catch (_) {}
          if (res.status >= 200 && res.status < 400) ok++;
          else errors++;
          return { ms, failed: false };
        } catch (e) {
          const ms = Date.now() - start;
          errors++;
          return { ms, failed: true };
        }
      })
    );
    for (const r of results) {
      latencies.push(r.ms);
    }
  }

  return { sent: count, ok, errors, latencies };
}

function percentile(sorted: number[], p: number): number {
  if (sorted.length === 0) return 0;
  const i = (p / 100) * (sorted.length - 1);
  const lo = Math.floor(i);
  const hi = Math.ceil(i);
  if (lo === hi) return sorted[lo];
  return sorted[lo] + (i - lo) * (sorted[hi] - sorted[lo]);
}

const CORS = {
  'Access-Control-Allow-Origin': '*',
  'Access-Control-Allow-Methods': 'POST, OPTIONS',
  'Access-Control-Allow-Headers': 'Content-Type, Authorization',
};

export default {
  async fetch(request: Request, env: Env): Promise<Response> {
    if (request.method === 'OPTIONS') {
      return new Response(null, { headers: CORS });
    }

    const url = new URL(request.url);
    if (url.pathname !== '/load-test-batch' || request.method !== 'POST') {
      return new Response(
        JSON.stringify({ error: 'Not found', path: url.pathname }),
        { status: 404, headers: { 'Content-Type': 'application/json' } }
      );
    }

    const auth = request.headers.get('Authorization');
    const token = auth?.startsWith('Bearer ') ? auth.slice(7) : null;
    if (!token || !env.JWT_SECRET) {
      return new Response(
        JSON.stringify({ error: 'Unauthorized' }),
        { status: 401, headers: { 'Content-Type': 'application/json' } }
      );
    }

    let payload: JWTPayload;
    try {
      payload = await verifyLoadTestToken(token, env.JWT_SECRET);
    } catch (e) {
      return new Response(
        JSON.stringify({ error: 'Invalid or expired token' }),
        { status: 401, headers: { 'Content-Type': 'application/json' } }
      );
    }

    let body: { url?: string; count?: number };
    try {
      body = (await request.json()) as { url?: string; count?: number };
    } catch {
      return new Response(
        JSON.stringify({ error: 'Invalid JSON body' }),
        { status: 400, headers: { 'Content-Type': 'application/json' } }
      );
    }

    const targetUrl = body.url;
    const count = typeof body.count === 'number' ? body.count : 0;
    if (!targetUrl || targetUrl !== payload.url || count < 1 || count > payload.countPerBatch) {
      return new Response(
        JSON.stringify({ error: 'Invalid url or count' }),
        { status: 400, headers: { 'Content-Type': 'application/json' } }
      );
    }

    const result = await runBatch(targetUrl, count);
    const sorted = [...result.latencies].sort((a, b) => a - b);
    const summary = {
      sent: result.sent,
      ok: result.ok,
      errors: result.errors,
      latencies: result.latencies,
      summary:
        sorted.length > 0
          ? {
              min: sorted[0],
              max: sorted[sorted.length - 1],
              avg: Math.round(sorted.reduce((a, b) => a + b, 0) / sorted.length),
              p50: Math.round(percentile(sorted, 50)),
              p95: Math.round(percentile(sorted, 95)),
              p99: Math.round(percentile(sorted, 99)),
            }
          : null,
    };

    return new Response(JSON.stringify(summary), {
      headers: { 'Content-Type': 'application/json', ...CORS },
    });
  },
};
